import { Injectable } from '@angular/core';
import { Transaction } from '../model/transaction.model';
import {
  TransactionCategoryIcon,
  TransactionTypeIcon,
  TransactionCreditDebitIcon,
  BaseTransactionIcon,
} from '../model/transaction-category.model';

@Injectable()
export class TransactionsIconCategoriesFactoryService {
  create(transaction: Transaction): BaseTransactionIcon {
    if (transaction.category || transaction.isPending) {
      return new TransactionCategoryIcon(transaction);
    } else if (transaction.type) {
      return new TransactionTypeIcon(transaction);
    } else {
      return new TransactionCreditDebitIcon(transaction);
    }
  }
}
